export default {
	data() {
		return {
			
			actionConfig:{
				title:'',
				summary:'',
				list:[],
				type:null,
				isCloseCancel:false,
				cancelColor:"#333333",
				isBorderColor:false,
				titleColor:"#333333",
				specClass: 'none',
			}
		}
	},
	methods: {
		toggleSpec() {
			if(this.actionConfig.specClass === 'show'){
				this.actionConfig.specClass = 'hide';
				setTimeout(() => {
					this.actionConfig.specClass = 'none';
				}, 250);
			}else if(this.actionConfig.specClass === 'none'){
				this.actionConfig.specClass = 'show';
			}
		},
		stopPrevent(){},
		itemClick(index,type){
			this.$emit("itemClick",index,type)
			this.toggleSpec()
		},
		cancelClick(){
			this.toggleSpec()
		},
		titleClick(type){
			this.$emit("titleClick",type)
			this.toggleSpec()
		}
	}
}
